import React, { useContext } from 'react';

import { ThemeContext } from '../../contexts/ThemeContext';

import './Certifications'
import CertificationsCard from './CertificationsCard';

import { certificationsData } from '../../data/certificationsData'
import contactsBlue from '../../assets/svg/contacts/contactsBlue.svg';

function Certifications() {

    const { theme } = useContext(ThemeContext);
    return (
        <div className="education" id="certifications" style={{backgroundColor: theme.secondary}}>
           
            <div className="education-body">
                <div className="education-image">
                    <img src={contactsBlue} alt=""/>
                </div>
                <div className="education-description">
                <h1 style={{color:theme.primary}}>Certifications</h1>
                    {certificationsData.map(edu => (
                        <CertificationsCard 
                            key={edu.id}
                            id={edu.id}
                            institution={edu.institution}
                            course={edu.course}
                            startYear={edu.startYear}
                            endYear={edu.endYear}
                        />
                    ))}
                </div>
                
            </div>
        </div>
    )
}

export default Certifications
